@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.new-expense{background-color:#a892ee;padding:1rem;margin:2rem auto;width:50rem;max-width:95%;border-radius:12px;text-align:center;box-shadow:0 1px 8px rgba(0,0,0,.25)}.new-expense__button{font:inherit;cursor:pointer;padding:1rem 2rem;border:1px solid #40005d;background-color:#40005d;color:#fff;border-radius:12px;margin-right:1rem}.new-expense__button:hover,.new-expense__button:active{background-color:#510674;border-color:#510674}.new-expense__controls{display:flex;flex-wrap:wrap;grid-gap:1rem;gap:1rem;margin-bottom:1rem;text-align:left}.new-expense__label{font-weight:bold;margin-bottom:.5rem;display:block}.new-expense__input{font:inherit;padding:.5rem;border-radius:6px;border:1px solid #ccc;width:20rem;max-width:100%}.new-expense__actions{text-align:right}.new-expense button.alternative{color:#220131;border-color:rgba(0,0,0,0);background-color:rgba(0,0,0,0)}.new-expense button.alternative:hover,.new-expense button.alternative:active{background-color:#ddb3f8}
.card{border-radius:12px;box-shadow:0 1px 8px rgba(0,0,0,.25)}
.expenses-filter{color:#fff;padding:0 1rem}.expenses-filter__control{display:flex;width:100%;align-items:center;justify-content:space-between;margin:1rem 0}.expenses-filter__label{font-weight:bold;margin-bottom:.5rem}.expenses-filter__select{font:inherit;padding:.5rem 3rem;font-weight:bold;border-radius:6px}
.expenses{padding:1rem;background-color:#1f1f1f;margin:2rem auto;width:50rem;max-width:95%}
.expenses-list{list-style:none;padding:0}.expenses-list__fallback{color:#fff;text-align:center}
.expense-date{display:flex;flex-direction:column;width:5.5rem;height:5.5rem;border:1px solid #ececec;background-color:#2a2a2a;color:#fff;border-radius:12px;align-items:center;justify-content:center}.expense-date__month{font-size:.75rem;font-weight:bold}.expense-date__year{font-size:.75rem}.expense-date__day{font-size:1.5rem;font-weight:bold}
.expense-item{display:flex;justify-content:space-between;align-items:center;padding:.5rem;margin:1rem 0;background-color:#4b4b4b;cursor:pointer}.expense-item__description{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;align-items:flex-end;flex-flow:column-reverse;justify-content:flex-start;flex:1 1}@media(min-width: 580px){.expense-item__description{flex-direction:row;align-items:center;justify-content:flex-start;flex:1 1}}.expense-item__title{color:#3a3a3a;font-size:1rem;flex:1 1;margin:0 1rem;color:#fff}@media(min-width: 580px){.expense-item__title{font-size:1.25rem}}.expense-item__amount{font-size:1rem;font-weight:bold;color:#fff;background-color:#40005d;border:1px solid #fff;padding:.5rem;border-radius:12px}@media(min-width: 580px){.expense-item__amount{font-size:1.25rem;padding:.5rem 1.5rem}}.expense-delete{text-align:center;padding:2rem}.expense-delete__text{color:#fff;margin-bottom:2rem}.expense-delete__button{font:inherit;cursor:pointer;padding:1rem 2rem;border:1px solid #40005d;background-color:#40005d;color:#fff;border-radius:12px;margin-right:1rem}.expense-delete__button:hover,.expense-delete__button:active{background-color:#510674;border-color:#510674}
.chart{padding:1rem;border-radius:12px;background-color:#f8dfff;text-align:center;display:flex;justify-content:space-around;height:10rem}.chart-bar{height:100%;display:flex;flex-direction:column;align-items:center}.chart-bar__inner{height:100%;width:100%;border:1px solid #313131;border-radius:12px;background-color:#c3b4f3;overflow:hidden;display:flex;flex-direction:column;justify-content:flex-end}.chart-bar__fill{background-color:#4826b9;width:100%;transition:all .3s ease-out}.chart-bar__label{font-weight:bold;font-size:.5rem;text-align:center}
