.expenses-filter {
  color: white;
  padding: 0 1rem;

  &__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  &__label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  &__select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
}