.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
  cursor: pointer;

  &__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;

    @media (min-width: 580px) {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  }

  &__title {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;

    @media (min-width: 580px) {
      font-size: 1.25rem;
    }
  }

  &__amount {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;

    @media (min-width: 580px) {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
}

.expense-delete {
  text-align: center;
  padding: 2rem;

  &__text {
    color: white;
    margin-bottom: 2rem;
  }

  &__button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;

    &:hover,
    &:active {
      background-color: #510674;
      border-color: #510674;
    }
  }
}